      h1 Derechos territoriales, autonomía cultural y consulta previa en proyectos turísticos

    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      p El desarrollo de proyectos turísticos en territorios étnicos requiere un enfoque integral basado en el respeto, la participación y la protección de los derechos colectivos reconocidos en la Constitución Política de 1991 y en instrumentos internacionales como el Convenio 169 de la Organización Internacional del Trabajo (OIT). Estos derechos garantizan que las comunidades indígenas, afrodescendientes, raizales y Rrom sean protagonistas activos en las decisiones que afectan su entorno, su cultura y su bienestar, evitando prácticas de exclusión o de apropiación cultural.
      p El turismo, cuando se desarrolla de forma planificada y con enfoque de derechos, puede ser un aliado estratégico para la preservación del patrimonio cultural y natural de las comunidades. Sin embargo, cuando se impone sin consulta previa o sin participación efectiva, puede convertirse en una fuente de conflicto, desplazamiento o pérdida de identidad. De allí la importancia de comprender los tres pilares fundamentales que orientan la relación entre turismo y comunidades étnicas: los derechos territoriales, la autonomía cultural y la consulta previa.
      p A continuación se explica de qué trata cada uno de estos pilares:
  
