    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      p El respeto a los derechos territoriales, la autonomía cultural y la consulta previa constituye la base para garantizar que el turismo no se convierta en una amenaza, sino en una herramienta de desarrollo comunitario, fortalecimiento de la identidad y conservación ambiental. Integrar estos principios en la planificación turística contribuye a construir una Colombia más inclusiva, equitativa y respetuosa de su diversidad cultural.
      p A continuación, se relacionan algunos de los proyectos turísticos que han estado sujetos a procesos de consulta previa en Colombia, según pronunciamientos recientes de la Corte Constitucional.

